.body {
  width: 100%;
  height: calc(100vh - 10px);
  height: calc(100dvh - 10px);

  background-color: rgb(0, 0, 0);
  /* background-image: linear-gradient(38.73deg, rgba(204, 0, 187, 0.35) 0%, rgba(201, 32, 184, 0) 50%), linear-gradient(141.27deg, rgba(0, 70, 209, 0) 50%, rgba(0, 70, 209, 0.35) 100%); */
  background-image: linear-gradient(
      38.73deg,
      var(--left-color) 0%,
      rgba(201, 32, 184, 0) 50%
    ),
    linear-gradient(141.27deg, rgba(0, 70, 209, 0) 50%, var(--right-color) 100%);
  transition: background-image 0.5s ease;
  position: relative;
  overflow: hidden;
  font-size: 2vw;
  /* background-image: linear-gradient(#0f0b10, #382f32); */
}

.about_header {
  font-size: 200px;

  color: rgba(255, 255, 255, 0.119);
}

@media screen and (max-width: 750px) {
  .about_header {
    font-size: 100px;
  }
}

@media screen and (max-width: 500px) {
  .about_header {
    font-size: 50px;
  }
}

