.profPic {
  /* width: 60vw; */
  /* height: 60vh; */
  max-width: 618px;
  width: 100%;
  height: auto;
  object-fit: contain;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: transform 1s ease;
}

.hello {
  /* width: 5em; */
  font-weight: bolder;
  color: rgba(255, 255, 255, 0.583);
  font-size: 20.1em;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);

  transition: transform 1s;

  background: linear-gradient(#ffffff94 0%, #ffffff00 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.im {
  /* width: 5em; */
  /* font-family: ""; */
  font-family: "Pacifico";
  /* font-weight: bold; */
  color: white;
  font-size: 6.5em;
  position: absolute;
  top: 55%;
  left: 35%;
  /* transform: translate(0, -50%); */
}

@media screen and (max-width: 1500px) {
  .im {
    font-size: 6.1em;
    left: auto;
    right: 0%;
  }
}

@media screen and (max-width: 750px) {
  .im {
    font-size: 10.1em;
    left: 0%;
  }
}
