.body {
  background-image: linear-gradient(
      38.73deg,
      rgba(204, 0, 187, 0.25) 0%,
      rgba(201, 32, 184, 0) 50%
    ),
    linear-gradient(
      141.27deg,
      rgba(0, 70, 209, 0) 50%,
      rgba(0, 70, 209, 0.25) 100%
    );
  background-color: black;
  /* height: 200vh; */
  width: 100%;
  display: flex;
  justify-self: start;
  flex-direction: column;
  align-items: center;

  color: white;
}

.alternative {
  /* height: 100vh; */
  padding: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: rgba(255, 255, 255, 0.29);
}

.alternative p {
  margin: 10px;
}

.cv {
  border: none;
  height: 100vh;
  height: 100dvh;
  width: 70%;
}

.cv iframe {
  height: 100vh;
  height: 100dvh;

  width: 100%;
}

.cv {
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  scrollbar-width: none;
  /* Firefox */
}

.cv::-webkit-scrollbar {
  display: none;
  /* Safari and Chrome */
  overflow: -moz-scrollbars-none;
}

.audio {
  width: 70vw;
  padding-block: 10px;
  position: absolute;
  left: 15%;
  bottom: -10px;
}

audio::-webkit-media-controls-enclosure {
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
}

@media screen and (max-width: 768px) {
  .cv {
    width: 100%;
  }
  .audio {
    width: 100vw;
    left: 0;
  }
}
